<template>
  <el-card class="liv-data-card" v-bind="globalProps">
    <template v-for="(_, name) in omit($slots, 'default')" #[name]="slotData">
      <slot :name="name" v-bind="slotData || {}" />
    </template>
    <!-- 这种方式会有bug，当slot是原始html标签且嵌套多个v-if时会渲染异常 -->
    <!-- <template v-for="slot in $slots.default?.()" :key="slot.scopeId">
      <div v-if="slot.type['name'] === 'LivDataTable'" class="liv-data-card__table">
        <component :is="slot"></component>
      </div>
      <component v-else :is="slot"></component>
    </template> -->
    <default-slot />
  </el-card>
</template>

<script lang="ts" setup>
import { useSlots, computed, h, inject } from 'vue';
import type { VNode, Component } from 'vue';
import { ElCard } from 'element-plus';
import { omit } from 'lodash-es';
import { DATA_CARD_KEY } from '@liv-web/constants';

/**
 * 组件配置
 */
defineOptions({
  name: 'LivDataCard',
});

/**
 * 全局属性
 */
const globalProps = inject(DATA_CARD_KEY, {});

/**
 * 组件插槽
 */
const slots = useSlots();

/**
 * 组件默认插槽
 */
const DefaultSlot = computed(() => {
  if (!slots.default) return null;
  return () => {
    return slots.default?.()?.map((vnode: VNode) => {
      if ((vnode.type as Component)?.name === 'LivDataTable') {
        return h('div', { class: 'liv-data-card__table' }, [vnode]);
      }
      return vnode;
    });
  };
});
</script>
